body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #58120D;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html,
body,
#root {
  display: inline-block;
  height: 100%;
  width: 100%;

  padding: 0px;
  margin: 0px;

  overflow: hidden;
  margin-bottom: 0px;

  font-size: 1em;
  line-height: 1.2em;
  font-family: 'Montserrat', sans-serif !important;
}

.App {
  text-align: left;
  background-color: #680309;
  color: #eEbF93;
  height: 100%;
}

.creme {
  color: #eEbF93;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #680309;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: left;
          align-items: left;
  -webkit-justify-content: left;
          justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
}

.BackgroundVermelhoEscuro {
  background-color: #3e0003;
}

#LogoAmal {
  display: inline-block;
  height: 164px;
  width: 165px;
  margin-left: -155px;
}

ul {
  list-style: none;
  padding: 0px; 
  margin: 0px;
}

ul li {
  display: inline-block;
  width: 100%;
  float: left;
}

.MenuCentral {
  display: inline-table; 
}

.MenuCentral li {
  text-align: center;
}

.half {
  display: -webkit-flex;
  display: flex;
  width: 50%;
  height: 100%;
  float: left;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#imagem {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.HomeButton,
button,
.btn {
  background-color: rgb(206, 193, 182);
  color: #400A07;
  padding: 1vh;
  border-radius: 4px;
  text-decoration: none;
  margin: 5px;
  display: inline-block;
}

.btn {
  display: inline-block;
  height: 25px !important;
  line-height: 30px;
  text-align: center;
  padding: 5px 5%;
}

.HomeButton:hover,
button:hover,
.btn:hover {
